body {
  font-size: 16px;
}

.search-container {
  background-color: #393978;
}

.search-form {
  min-width: 300px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 8px 0;
  display: flex;
}

.search-input {
  width: 190px;
  filter: drop-shadow(0 4px 4px #00000026);
  border: 1px solid #ffffff4d;
  border-radius: 4px;
  outline: #0000;
  margin-right: 8px;
  padding: 8px 5px;
  font-size: 16px;
}

.search-input::placeholder {
  font-size: 16px;
}

.search-button {
  filter: drop-shadow(0 4px 4px #00000026);
  cursor: pointer;
  border: 1px solid #ffffff4d;
  border-radius: 4px;
  padding: 8px 5px;
  font-size: 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.search-button:hover, .search-button:focus {
  background-color: #fff;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw-80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  display: grid;
}

.photo-card {
  height: 400px;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

